.container-shop-list {
  max-width: 375px;
  margin: 0 auto;
  padding: 40px 20px;
}

.shop-list-title {
  line-height: 1.18;
  font-size: 32px;
  letter-spacing: -1.28px;
  margin-bottom: 40px;
}

.last-word-shop-list {
  color: #4f2ee8;
}

.books-container-shop-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.book-card-shop-list {
  display: flex;
  border-radius: 16px;
  border: 2px solid rgba(79, 46, 232, 0.4);
  background: #fff;
  padding: 14px;
  gap: 14px;
  position: relative;
}

.img-shop-list {
  width: 100px;
  height: 142px;
  border-radius: 8px;
}

.right-card-part {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title-shop-list {
  max-width: 144px;
  height: 18px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
  letter-spacing: -0.64px;
  margin-bottom: 4px;
}

.category-shop-list {
  color: #b4afaf;
  font-size: 12px;
  line-height: 1.167;
  letter-spacing: 0.36px;
  margin-bottom: 8px;
  width: 165px;
  height: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.description-shop-list {
  width: 193px;
  height: 68px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 14px;
  line-height: 1.286;
  letter-spacing: -0.56px;
}

.card-bottom-shop-list {
  display: flex;
  justify-content: space-between;
}

.author-shop-list {
  color: #b4afaf;
  font-size: 12px;
  font-style: italic;
  line-height: 1.167;
  letter-spacing: -0.48px;
}

.links-shop-list {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.delete-btn-shop-list {
  position: absolute;
  right: 14px;
  top: 14px;
  width: 28px;
  height: 28px;
  border-radius: 50;
  background-color: #4f2ee8;
  border: 0;
}

@media screen and (min-width: 768px) {
  .container-shop-list {
    max-width: 744px;
    padding: 40px 32px;
  }

  .shop-list-title {
    font-size: 48px;
    line-height: 1.08;
    letter-spacing: -1.92px;
  }

  .book-card-shop-list {
    padding: 24px;
    gap: 24px;
  }

  .img-shop-list {
    width: 116px;
    height: 165px;
  }

  .title-shop-list {
    max-width: 458px;
  }

  .category-shop-list {
    margin-bottom: 14px;
    width: 458px;
  }

  .description-shop-list {
    width: 508px;
    height: 53px;
  }

  .links-shop-list {
    gap: 16px;
  }

  .delete-btn-shop-list {
    right: 24px;
    top: 24px;
    width: 34px;
    height: 34px;
  }
}

@media screen and (min-width: 1440px) {
.container-shop-list {
  max-width: 996px;
  padding: 0;
  margin-left: auto;
}
.description-shop-list {
  width: 808px;
}
}